'use client';

import { useState, useEffect, FormEvent } from 'react';
import clsx from 'clsx';
import { getProducts } from '@/lib/services/productService';
import {
  getPromos,
  getProductPromoRelations,
  assignPromoToProduct,
  PromoProductCreate,
} from '@/lib/services/promoService';
import type { Producto, Promo } from '@/lib/types';

export default function AsignarPromosPage() {
  const [productos, setProductos] = useState<Producto[]>([]);
  const [promos, setPromos]         = useState<Promo[]>([]);
  const [relations, setRelations]   = useState<PromoProductCreate[]>([]);

  const [productoId, setProductoId] = useState<number | ''>('');
  const [promoId, setPromoId]       = useState<number | ''>('');
  const [msg, setMsg]               = useState<{ ok?: string; err?: string }>({});

  useEffect(() => {
    (async () => {
      const pr = await getProducts(); setProductos(pr.products);
      const pm = await getPromos();   setPromos(pm.promotions);
      const rel = await getProductPromoRelations(); setRelations(rel.products);
    })();
  }, []);

  const reloadRelations = async () => {
    const rel = await getProductPromoRelations();
    setRelations(rel.products);
  };

  const handleSubmit = async (e: FormEvent) => {
    e.preventDefault();
    setMsg({});
    if (productoId === '' || promoId === '') {
      return setMsg({ err: 'Selecciona un producto y una promoción.' });
    }
    try {
      await assignPromoToProduct({
        idProducto: productoId,
        idPromo: promoId,
      });
      setMsg({ ok: '¡Promoción asignada correctamente!' });
      setProductoId(''); setPromoId('');
      await reloadRelations();
    } catch {
      setMsg({ err: 'Error al asignar la promoción.' });
    }
  };

  return (
    <main className="min-h-screen p-6 bg-gray-50">
      <div className="mx-auto max-w-3xl space-y-8">
        <h1 className="text-3xl font-bold text-gray-800">
          Asignar Promoción a Producto
        </h1>

        <section className="bg-white p-6 rounded-lg shadow">
          <form onSubmit={handleSubmit} className="space-y-6">
            <Select
              label="Producto"
              value={productoId}
              onChange={setProductoId}
              placeholder="Selecciona un producto…"
              options={productos.map(p => ({ value: p.id, label: p.name }))}
              color="yellow"
            />

            <Select
              label="Promoción"
              value={promoId}
              onChange={setPromoId}
              placeholder="Selecciona una promoción…"
              options={promos.map(p => ({
                value: p.id,
                label: `${p.nombre} — ${p.descripcion}`,
              }))}
              color="blue"
            />

            {msg.err && <p className="text-sm text-red-600">{msg.err}</p>}
            {msg.ok && <p className="text-sm text-green-600">{msg.ok}</p>}

            <button
              type="submit"
              className="w-full rounded-lg bg-blue-600 py-2 text-white font-semibold hover:bg-blue-700"
            >
              Asignar Promoción
            </button>
          </form>
        </section>

        <section className="bg-white p-6 rounded-lg shadow">
          <h2 className="mb-4 text-2xl font-semibold text-gray-700">
            Asignaciones existentes
          </h2>
          {relations.length === 0 ? (
            <p className="text-gray-500">No hay asignaciones aún.</p>
          ) : (
            <ul className="space-y-2">
              {relations.map((r, idx) => {
                const prod = productos.find(p => p.id === r.idProducto);
                const promo = promos.find(p => p.id === r.idPromo);
                return (
                  <li key={idx} className="flex justify-between rounded border px-4 py-2">
                    <span>
                      <strong>{prod?.name ?? r.idProducto}</strong> ↔{' '}
                      <em>{promo?.nombre ?? r.idPromo}</em>
                    </span>
                  </li>
                );
              })}
            </ul>
          )}
        </section>
      </div>
    </main>
  );
}

type SelectOption = { value: number; label: string };

function Select({
  label, value, onChange, placeholder, options, color,
}: {
  label: string;
  value: number | '';
  onChange: (v: number | '') => void;
  placeholder: string;
  options: SelectOption[];
  color: 'yellow' | 'blue';
}) {
  return (
    <div className="space-y-1">
      <label className="block text-sm font-medium text-gray-700">{label}</label>
      <select
        value={value}
        onChange={e => onChange(e.target.value === '' ? '' : Number(e.target.value))}
        className={clsx(
          'block w-full rounded-md px-3 py-2 text-base border-2 focus:outline-none focus:ring-2 focus:ring-offset-1',
          color === 'yellow'
            ? 'border-yellow-400 focus:ring-yellow-300'
            : 'border-blue-400 focus:ring-blue-300',
          !value && 'text-gray-400'
        )}
      >
        <option value="">{placeholder}</option>
        {options.length === 0 && <option disabled>Sin datos aún</option>}
        {options.map(o => (
          <option key={o.value} value={o.value}>
            {o.label}
          </option>
        ))}
      </select>
    </div>
  );
}
